}

:root {
  --container: 1160px;
  --main-font: "Inter", sans-serif;
  --roboto: "Roboto", sans-serif;
  --montserrat: "Montserrat", sans-serif;
  --font-size: 20px;
  --color-1: #252525;
  --color-2: #000;
  --color-3: #2B2B2F;
  --color-4: #005FA9;
  --white: #FFF;
  --bg-color: #fff;
  --gradient-1: linear-gradient(180deg, #005fa9 0%, #0190ff 100%);
  --radius-1: 10px;
  --radius-2: 30px;
  --shadow-1: 0 4px 20px 0 rgba(0, 0, 0, 0.1);
}

html {
  font-family: var(--main-font);
  font-size: var(--font-size);
  font-weight: 400;
  line-height: 1;
  color: var(--color-2);
}

html,
body {
  scrollbar-gutter: stable;
  background-color: var(--bg-color);
}

body {
  position: relative;
  max-width: 100vw !important;
  margin: 0;
}

body * {
  font-family: var(--main-font);
  font-weight: 400;
  line-height: normal;
}

body.no-scroll {
  overflow: hidden;
}

main {
  position: relative;
  z-index: 3;
  min-height: 55vh;
  background-color: var(--bg-color);
}

main.layer-up {
  z-index: 5;
}

section {
  position: relative;
  z-index: 1;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul,
ol,
li {
  display: block;
  padding: 0;
  margin: 0;
}

strong,
b {
  font-weight: bold;
}

* {
  box-sizing: border-box;
}

*::before,
*::after {
  box-sizing: border-box;
}

a,
a:hover,
a:active,
a:focus,
button,
input,
button:hover,
button:active,
button:focus,
input:focus,
input:active {
  text-decoration: none;
  outline: none;
}

a {
  color: inherit;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type=number],
input[type=number]:hover,
input[type=number]:focus {
  appearance: none;
  -moz-appearance: textfield;
}

img,
picture,
figure {
  display: block;
  margin: 0;
}

pre {
  color: #fff;
}

.container {
  position: relative;
  max-width: var(--container);
  width: 100%;
  margin: 0 auto;
}

.pointer {
  cursor: pointer;
}

.upper {
  text-transform: uppercase;
}

.validate {
  border-color: darkred !important;
}

.download-presentation {
  position: fixed;
  bottom: 50px;
  right: 0;
  z-index: 4;
  width: 528px;
  height: 147px;
  overflow-x: hidden;
}

.download-presentation a {
  display: flex;
  align-items: center;
  gap: 35px;
  width: 100%;
  height: 100%;
  padding-left: 63px;
  border-radius: 100px;
  background: var(--gradient-1);
  transform: translateX(118px);
}

.download-presentation span {
  max-width: 175px;
  font-family: var(--montserrat);
  color: var(--white);
  font-size: 24px;
  font-weight: 600;
  line-height: 1;
}

.main-title {
  font-size: 64px;
  font-weight: 800;
  color: var(--white);
  line-height: normal;
  user-select: none;
}

.second-title {
  color: var(--color-1);
  font-size: 46px;
  font-weight: 700;
  line-height: normal;
  user-select: none;
}

.with-line {
  position: relative;
  display: flex;
  align-items: center;
  gap: 60px;
  white-space: nowrap;
}

.with-line::after {
  content: "";
  flex-grow: 0;
  width: 100%;
  height: 1px;
  background-color: #000;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 55px;
  padding: 15px 0 16px;
  font-family: var(--roboto);
  color: var(--white);
  font-size: var(--font-size);
  font-weight: 500;
  line-height: normal;
  background: var(--gradient-1);
  border: 0;
  border-radius: var(--radius-1);
  cursor: pointer;
  transition: box-shadow .3s ease-out;
}

.btn:hover,
.btn:focus-visible {
  box-shadow: var(--shadow-1);
}

.btn.transparent {
  border: 1px solid var(--white);
  background: rgba(0,0,0,0);
}

.tablet {
  display: none;
}

.mobile {
  display: none;
}

.socials {
  display: flex;
  align-items: center;
  gap: 11px;
}

.socials li:hover .svg-ico,
.socials li:focus-visible .svg-ico {
  color: var(--green);
}

.socials .svg-ico {
  color: #fff;
  transition: color .3s ease-in-out;
}

.form__col {
  position: relative;
}

.form__col .msg {
  position: absolute;
  bottom: 0;
  left: 10px;
  display: none;
  width: max-content;
  font-size: 10px;
  color: red;
  letter-spacing: .1em;
  transform: translateY(100%);
}

.form-input,
.form-textarea {
  width: 100%;
  height: 35px;
  padding-bottom: 15px;
  font-size: 18px;
  color: var(--color-2);
  border: none;
  border-bottom: 1px solid rgba(197,197,197,.4);
  border-radius: 0;
  transition: border-color .3s ease-in-out;
}

.form-input::placeholder,
.form-textarea::placeholder {
  color: #999;
}

.form-input:hover,
.form-input:focus-visible,
.form-textarea:hover,
.form-textarea:focus-visible {
  border-color: var(--color-4);
}

.form-textarea {
  resize: none;
  scrollbar-width: none;
}

.form-textarea::-webkit-scrollbar {
  width: 0;
}

.form-textarea:focus-visible {
  outline: 0;
}

.from__agreement {
  margin-top: -14px;
  font-size: 12px;
  color: rgba(43,43,47,.5);
}

.loader::before {
  content: "";
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  background: rgba(255,255,255,.6) url("../img/icons/spinner.svg") center/100px no-repeat;
}

.disabled {
  pointer-events: none;
  background: gray !important;
}

.hidden {
  display: none !important;
}

.custom-radio {
  cursor: pointer;
}

.custom-radio:first-child {
  border-radius: 10px 0 0 10px;
}

.custom-radio:first-child .custom-radio-text,
.custom-radio:first-child .custom-radio-text::after {
  border-radius: 10px 0 0 10px;
}

.custom-radio:last-of-type {
  border-radius: 0 10px 10px 0;
}

.custom-radio:last-of-type .custom-radio-text,
.custom-radio:last-of-type .custom-radio-text::after {
  border-radius: 0 10px 10px 0;
}

.custom-radio.disabled {
  pointer-events: none;
}

.custom-radio.disabled .custom-radio-text {
  border-color: gray !important;
  opacity: .7;
  pointer-events: none;
}

.custom-radio.disabled .custom-radio-text::after {
  background-color: gray !important;
  pointer-events: none;
}

.custom-radio input {
  display: none;
}

.custom-radio input:checked~.custom-radio-text span {
  font-weight: 700;
}

.custom-radio input:checked~.custom-radio-text::after {
  opacity: 1;
}

.custom-radio .custom-radio-text {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  border: 1px solid var(--green);
  transition: font-weight .3s ease-in-out,background-color .3s ease-in-out;
}

.custom-radio .custom-radio-text::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: var(--green-gradient);
  opacity: 0;
  transition: opacity .3s ease-in-out;
}

.custom-radio .custom-radio-text span {
  position: relative;
  z-index: 1;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  user-select: none;
}

.custom-radio-two {
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
}

.custom-radio-two:hover .custom-radio-text::after,
.custom-radio-two:focus-visible .custom-radio-text::after {
  background-color: var(--green);
}

.custom-radio-two.disabled {
  background: rgba(0,0,0,0) !important;
}

.custom-radio-two.disabled .custom-radio-circle {
  border-color: gray !important;
}

.custom-radio-two.disabled .custom-radio-text {
  text-decoration: line-through;
}

.custom-radio-two input {
  display: none;
}

.custom-radio-two input:checked~.custom-radio-circle {
  border-color: var(--green);
}

.custom-radio-two input:checked~.custom-radio-circle::after {
  background-color: var(--green);
}

.custom-radio-two .custom-radio-circle {
  position: relative;
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  border: 1px solid #e7eef1;
  border-radius: 50%;
  transition: border-color .3s ease-in-out;
}

.custom-radio-two .custom-radio-circle::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 14px;
  height: 14px;
  margin: auto;
  border-radius: 50%;
  background-color: rgba(0,0,0,0);
  transition: background-color .3s ease-in-out;
}

.custom-radio-two .custom-radio-text {
  position: relative;
  flex-shrink: 0;
  width: max-content;
  color: #fff;
  font-size: 13px;
  font-weight: 500;
  line-height: 1;
}

.custom-radio-two .custom-radio-text::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: rgba(0,0,0,0);
  transition: background-color .3s ease-in-out;
}

.custom-checkbox {
  position: relative;
  display: flex;
  align-items: center;
  gap: 13px;
  cursor: pointer;
}

.custom-checkbox input {
  display: none;
}

.custom-checkbox input:checked~.custom-checkbox__checkbox::before {
  opacity: 1;
}

.custom-checkbox input.not-checked~.custom-checkbox__checkbox {
  border-color: red;
}

.custom-checkbox .custom-checkbox__checkbox {
  position: absolute;
  left: 0;
  width: 17px;
  height: 17px;
  border-radius: 3px;
  border: 1px solid rgba(255,255,255,.1);
  background-color: #fff;
  transition: border-color .3s ease-in-out;
}

.custom-checkbox .custom-checkbox__checkbox::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  background: #fff url("../img/icons/check.svg") center/13px no-repeat;
  opacity: 0;
  transition: opacity .3s ease-in-out;
}

.custom-checkbox .custom-checkbox__text {
  color: #fff;
  font-size: 13px;
  font-weight: 500;
  line-height: 1;
  padding-left: 30px;
  user-select: none;
}

.custom-checkbox .custom-checkbox__text a {
  color: var(--green);
}

.custom-checkbox .custom-checkbox__text a:hover,
.custom-checkbox .custom-checkbox__text a:focus-visible {
  text-decoration: underline;
}

.form-label {
  position: relative;
}

.form-label input::placeholder,
.form-label textarea::placeholder {
  font-size: 0;
}

.form-label input:active~label,
.form-label input:focus~label,
.form-label textarea:active~label,
.form-label textarea:focus~label {
  color: #fff;
  background-color: var(--green);
  transform: translateY(-50%);
}

.form-label input:active~label .green,
.form-label input:focus~label .green,
.form-label textarea:active~label .green,
.form-label textarea:focus~label .green {
  color: red !important;
}

.form-label input:active~label:not(:placeholder-shown)~label,
.form-label input:focus~label:not(:placeholder-shown)~label,
.form-label textarea:active~label:not(:placeholder-shown)~label,
.form-label textarea:focus~label:not(:placeholder-shown)~label {
  transform: translateY(-50%);
  background-color: var(--green);
  color: #fff;
}

.form-label input:active~label:not(:placeholder-shown)~label .green,
.form-label input:focus~label:not(:placeholder-shown)~label .green,
.form-label textarea:active~label:not(:placeholder-shown)~label .green,
.form-label textarea:focus~label:not(:placeholder-shown)~label .green {
  color: red !important;
}

.form-label input:active~label:hover~label,
.form-label input:focus~label:hover~label,
.form-label textarea:active~label:hover~label,
.form-label textarea:focus~label:hover~label {
  color: #fff;
}

.form-label input:active~label:hover~label .green,
.form-label input:focus~label:hover~label .green,
.form-label textarea:active~label:hover~label .green,
.form-label textarea:focus~label:hover~label .green {
  color: red !important;
}

.form-label input:hover~label,
.form-label textarea:hover~label {
  color: #fff;
}

.form-label label {
  position: absolute;
  top: 0;
  left: 25px;
  padding: 5px;
  color: rgba(255,255,255,.1);
  background-color: rgba(0,0,0,0);
  border-radius: 6px;
  transform: translateY(65%);
  pointer-events: none;
  transition: transform .3s ease-in-out,color .3s ease-in-out,background-color .3s ease-in-out;
}

.form-label label,
.form-label label * {
  height: 26px;
  font-size: 16px;
  line-height: 1;
  cursor: auto;
}

.slider-dots {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.slider-dots li {
  width: 57.5px;
  height: 4px;
  border-radius: 0;
  background-color: #e8e8e8;
  cursor: pointer;
  transition: background-color .3s ease-in-out;
}

.slider-dots li.slick-active {
  background-color: var(--color-4);
}

.slider-dots li button {
  font-size: 0;
  background-color: rgba(0,0,0,0);
  border: 0;
}

.slick-arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background-color: var(--green);
  border-radius: 50%;
  cursor: pointer;
  transition: background-color .3s ease-in-out,box-shadow .3s ease-in-out;
}

.slick-arrow:hover,
.slick-arrow:focus-visible {
  box-shadow: var(--green-shadow);
}

.slick-arrow.arrow-left svg {
  transform: translateX(-2px);
}

.slick-arrow.arrow-right svg {
  transform: translateX(2px);
}

.slick-arrow.slick-disabled {
  background-color: gray;
  pointer-events: none;
}

.list-1 {
  display: flex;
  flex-wrap: wrap;
  gap: 45px 40px;
}

.list-1 .item {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: calc(50% - 20px);
  min-height: 234px;
  padding: 24px 20px;
  background-color: var(--white);
  border-radius: var(--radius-1);
  box-shadow: 0 4px 20px 0 rgba(0,0,0,.1);
}

.list-1 .item::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
  border-radius: var(--radius-1);
  background: var(--color-4) url("../img/services/services-item-bg.png") center/cover no-repeat;
  opacity: 0;
  transition: opacity .3s ease-in-out;
}

.list-1 .item:hover::after,
.list-1 .item:focus-visible::after {
  opacity: 1;
}

.list-1 .item:hover .item-title,
.list-1 .item:hover .item-description,
.list-1 .item:focus-visible .item-title,
.list-1 .item:focus-visible .item-description {
  color: var(--white);
}

.list-1 .item:hover .item-tags li,
.list-1 .item:focus-visible .item-tags li {
  color: var(--color-2);
  background-color: var(--white);
}

.list-1 .item-title {
  position: relative;
  z-index: 1;
  max-width: 50%;
  font-size: 24px;
  font-weight: 800;
  word-break: break-all;
  color: var(--color-2);
  transition: color .3s ease-in-out;
}

.list-1 .item-description {
  position: relative;
  z-index: 1;
  transition: color .3s ease-in-out;
}

.list-1 .item-tags {
  position: relative;
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  max-width: 50%;
}

.list-1 .item-tags li {
  padding: 5px 12px;
  color: var(--white);
  font-size: 14px;
  background-color: var(--color-4);
  border-radius: var(--radius-2);
  transition: background-color .3s ease-in-out,color .3s ease-in-out;
}

.list-1 .item-image {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  display: flex;
  align-items: flex-end;
  width: 20.8203206329vw;
  height: 19.6309383589vw;
  background-color: #0a2546;
  transform: rotate(-16deg) translate(42.03%, -42.3%);
  border-radius: var(--radius-2);
}

.list-1 .item-image img {
  width: 100%;
  height: max-content;
  border-radius: var(--radius-2);
  object-fit: cover;
}

.list-1 .item-link {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
}

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}

@keyframes slideInLeft {
  from {
    transform: translate3d(-100%, 0, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.slideInLeft {
  animation-name: slideInLeft;
}

@keyframes slideInUp {
  from {
    transform: translate3d(0, 100%, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.slideInUp {
  animation-name: slideInUp;
}

@keyframes slideOutLeft {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: translate3d(-100%, 0, 0);
  }
}

.slideOutLeft {
  animation-name: slideOutLeft;
}

@keyframes slideOutUp {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: translate3d(0, -100%, 0);
  }
}

.slideOutUp {
  animation-name: slideOutUp;
}

@keyframes slideOutRight {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: translate3d(100%, 0, 0);
  }
}

.slideOutRight {
  animation-name: slideOutRight;
}

@keyframes slideInRight {
  from {
    transform: translate3d(100%, 0, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.slideInRight {
  animation-name: slideInRight;
}

@keyframes slideOutDown {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: translate3d(0, 100%, 0);
  }
}

.slideOutDown {
  animation-name: slideOutDown;
}

@keyframes slideInDown {
  from {
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.slideInDown {
  animation-name: slideInDown;
}

.custom-select {
  position: relative;
}

.custom-select.open .select-title {
