}

.select-title:hover,
.select-title:focus-visible {
  border-color: var(--yellow);
}

.select-title::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  background: var(--green-gradient);
  opacity: 0;
  transition: opacity .3s ease-in-out;
}

.select-title>* {
