  z-index: 1;
}

.select-title__text {
  flex-shrink: 0;
  font-size: inherit;
  color: inherit;
  line-height: inherit;
  user-select: none;
}

.select-title__icon {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  margin-right: -4px;
  color: inherit;
  transition: transform .3s ease-in-out;
}

.select-title__icon svg {
  transition: color .3s ease-in-out;
}

.select-content {
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 2;
  display: flex;
  flex-direction: column;
  padding: 8px 15px 8px 0;
  background-color: rgba(0,0,0,0);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255,255,255,.7);
  border-top: 0;
  border-radius: 0 0 10px 10px;
  opacity: 0;
  transform: scale(0);
  transition: opacity .3s ease-in-out;
}

.select-content.no-scroll .select-content_wrap {
  overflow-y: hidden;
}

.select-content_overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  margin: auto;
  z-index: 99;
