  display: flex;
  cursor: pointer;
}

.select-content__wrapper.disabled {
  pointer-events: none;
}

.select-content__wrapper input {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

