.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  display: none;
  background: linear-gradient(0deg, rgba(33, 31, 32, 0.5) 0%, rgba(68, 64, 63, 0.5) 100%);
  backdrop-filter: blur(2.5px);
  cursor: pointer;
}

.modal__close {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  color: var(--yellow);
  cursor: pointer;
}

.modal__close:hover .svg-ico {
  color: var(--yellow-hover);
}

.modal__close .svg-ico {
  color: var(--yellow);
  transition: color .3s ease-in-out;
}

.modal__content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 11;
  overflow-y: scroll;
  max-width: 700px;
  width: 100%;
  max-height: 90vh;
  height: max-content;
  margin: auto;
  padding: 40px 55px;
  background: var(--color-4) url("../img/feedback-bg.png") center/cover no-repeat;
  border-radius: 10px;
  cursor: default;
  scrollbar-width: none;
}

.modal__content::-webkit-scrollbar {
  width: 0;
}

.modal__header {
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-width: 497px;
  margin: 0 auto 30px;
}

.modal__title {
  text-align: center;
  font-size: 40px;
  font-weight: 600;
  color: #fff;
  text-transform: uppercase;
}

.modal__title.red {
  color: red;
  font-weight: 500;
}

.modal__description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin: 0 auto;
  text-align: center;
  color: var(--white);
}

.modal__description a {
  text-decoration: underline;
}

.modal__description .shower-desc {
  display: none;
}

.modal__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: max-content;
  margin: 0 auto 30px;
}

.modal__form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 400px;
  width: 100%;
  margin: 0 auto;
  padding: 30px 15px 20px;
  border-radius: var(--radius-1);
  background-color: var(--white);
}

.modal__form-row {
  position: relative;
  width: 100%;
}

.modal__form-row .msg {
  position: absolute;
  bottom: 0;
  left: 20px;
  display: none;
  font-size: 10px;
  color: red;
  letter-spacing: .1em;
  transform: translateY(calc(100% + 2px));
}

.modal-input,
.modal-textarea {
  width: 100%;
  font-size: 17px;
  color: #fff;
  background-color: rgba(0,0,0,0);
  border: 1px solid rgba(255,255,255,.7);
  border-radius: 30px;
  transition: border-color .3s ease-in-out;
}

.modal-input:hover,
.modal-textarea:hover {
  border-color: var(--yellow-hover);
}

.modal-input:focus-visible,
.modal-textarea:focus-visible {
  outline: 0;
  border-color: var(--yellow);
}

.modal-input::placeholder,
.modal-textarea::placeholder {
  color: rgba(255,255,255,.7);
}

.modal-input {
  padding: 13px 20px;
}

.modal-textarea {
  height: 100px;
  padding: 16px 20px;
  resize: none;
}

.modal__price {
  display: flex;
  justify-content: center;
  gap: 15px;
  text-transform: uppercase;
}

.modal__submit {
  width: 100%;
  height: 60px;
}

.modal-checkout-notice {
  display: block;
  transition: opacity .3s ease-in-out;
}

.modal-checkout-notice .modal__content {
  animation-duration: .5s;
}

.modal-checkout-notice .modal__description {
  max-width: 400px;
  font-size: 15px;
}

.modal-checkout-notice .modal__body {
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-height: 64vh;
  margin-bottom: 0;
  scrollbar-width: thin;
  scrollbar-color: var(--yellow) var(--grey);
}

.modal-checkout-notice .modal__body::-webkit-scrollbar-thumb {
  background: var(--yellow);
}

.modal-checkout-notice .modal__body::-webkit-scrollbar {
  width: 6px;
}

.modal-checkout-notice .modal__body::-webkit-scrollbar-track {
  background: var(--grey);
}

.modal-checkout-notice .modal__body-row {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
}

.modal-checkout-notice .modal__body-row:last-child {
  margin-bottom: 4px;
}

.modal-checkout-notice .modal__body-row__title {
  font-size: 18px;
  font-weight: 600;
  color: #fff;
}

.modal-checkout-notice .modal__body-list {
  display: flex;
  flex-direction: column;
}

.modal-checkout-notice .modal__body-list .list-item {
  display: flex;
  align-items: center;
  gap: 30px;
  padding: 15px 20px;
  border-radius: 10px;
  box-shadow: 0 2px 4px 0 rgba(43,40,40,.6);
  transition: box-shadow .3s ease-in-out;
}

.modal-checkout-notice .modal__body-list .list-item:hover,
.modal-checkout-notice .modal__body-list .list-item:focus-visible {
  box-shadow: 0 4px 20px 0 rgba(43,40,40,.6);
}

.modal-checkout-notice .modal__body-list .list-item:hover .item__image img,
.modal-checkout-notice .modal__body-list .list-item:focus-visible .item__image img {
  transform: scale(1.1);
}

.modal-checkout-notice .modal__body-list .list-item:hover .item__title a,
.modal-checkout-notice .modal__body-list .list-item:focus-visible .item__title a {
  color: var(--green);
}

.modal-checkout-notice .modal__body-list .list-item:hover .item__total-price .price,
.modal-checkout-notice .modal__body-list .list-item:focus-visible .item__total-price .price {
  color: var(--green);
}

.modal-checkout-notice .modal__body-list .item__image {
  flex-shrink: 0;
  width: 120px;
  height: 90px;
  background: var(--grey-gradient);
  border-radius: 10px;
  overflow: hidden;
}

.modal-checkout-notice .modal__body-list .item__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
  transition: transform .3s ease-in-out;
}

.modal-checkout-notice .modal__body-list .item__content {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-right: auto;
}

.modal-checkout-notice .modal__body-list .item__title a {
  color: #fff;
  font-size: 16px;
  font-weight: 700;
  line-height: 1;
  transition: color .3s ease-in-out;
}

.modal-checkout-notice .modal__body-list .item__variations {
  display: flex;
  flex-direction: column;
}

.modal-checkout-notice .modal__body-list .variation-item {
  display: flex;
  align-items: center;
  gap: 5px;
}

.modal-checkout-notice .modal__body-list .variation-item .item-name,
.modal-checkout-notice .modal__body-list .variation-item .item-value {
  flex-shrink: 0;
}

.modal-checkout-notice .modal__body-list .variation-item .item-name {
  font-weight: 600;
  color: #fff;
}

.modal-checkout-notice .modal__body-list .item__total-price .price {
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  line-height: 1;
  -webkit-transition: color .3s ease-in-out;
  transition: color .3s ease-in-out;
}

.modal-checkout-notice .modal__body-list .item__remove {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 27px;
  height: 27px;
  padding: 0;
  border: 1.5px solid var(--green);
  border-radius: 50%;
  background-color: rgba(0,0,0,0);
  cursor: pointer;
  transition: box-shadow .3s ease-out;
}

.modal-checkout-notice .modal__body-list .item__remove:hover,
.modal-checkout-notice .modal__body-list .item__remove:focus-visible {
  box-shadow: var(--green-shadow);
}

.modal-checkout-notice .modal__body-list .item__remove .svg-ico {
  color: #fff;
}

#notice_not_valid .modal__description {
