@media screen and (max-width: 575.98px) {
  .custom-select.open .select-content_wrap {
    animation-name: SelectContentZoomIn;
  }

  .select-content {
    position: fixed;
    top: 0;
    bottom: 0;
    z-index: 100;
    width: 100vw;
    max-height: none !important;
    height: 100vh;
    margin: auto;
    padding: 0;
    border: 0;
    border-radius: 0;
    backdrop-filter: none;
    transition: none;
  }

  .select-content_wrap {
    z-index: 100;
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: calc(100vw - 40px);
    max-height: calc(100vh - 40px);
    height: max-content;
    margin: auto;
    padding: 20px 10px;
    border: 1px solid rgba(255,255,255,.7);
    border-radius: 10px;
    animation-duration: .5s;
    animation-fill-mode: both;
    animation-name: SelectContentZoomOut;
  }

  .select-content__radio {
    background-color: rgba(73,69,68,.3);
    border: 1px solid rgba(255,255,255,.5);
    border-radius: 7px;
  }

  .menu .container {
    gap: 15px;
  }

  .modal__content {
    width: calc(100vw - 40px);
    padding: 30px 20px;
  }

  .modal-checkout-notice .modal__content {
    padding: 30px 10px;
  }

  .modal-checkout-notice .modal__body {
    padding: 0 10px;
  }

  .modal-checkout-notice .modal__body-row__title {
    font-size: 16px;
  }

  .modal-checkout-notice .modal__body-list .list-item {
    position: relative;
    display: grid;
    grid-template-columns: calc((100vw - 110px)*.375) calc((100vw - 110px)*.625 - 10px);
    gap: 0 10px;
    padding: 15px;
  }

  .modal-checkout-notice .modal__body-list .item__image {
    grid-row: 1/span 2;
    width: calc((100vw - 110px)*.375);
    height: calc((100vw - 110px)*.375/1.35);
  }

  .modal-checkout-notice .modal__body-list .item__content {
    width: calc((100vw - 110px)*.625 - 10px);
  }

  .modal-checkout-notice .modal__body-list .item__title a {
    max-width: calc(100% - 30px);
    font-size: 14px;
  }

  .modal-checkout-notice .modal__body-list .variation-item .item-name {
    font-size: 14px;
  }

  .modal-checkout-notice .modal__body-list .variation-item .item-value {
    font-size: 14px;
  }

  .modal-checkout-notice .modal__body-list .item__total-price {
    grid-column: 2;
  }

  .modal-checkout-notice .modal__body-list .item__total-price .price {
    font-size: 18px;
  }

  .modal-checkout-notice .modal__body-list .item__remove {
    position: absolute;
    top: 0;
    right: 15px;
    bottom: 0;
    width: 24px;
    height: 24px;
    margin: auto;
  }

  .modal-checkout-notice .modal__body-list .item__remove .svg-ico {
    width: 16px;
    height: 23px;
  }

  #notice_not_valid .modal__body-item {
    font-size: 14px;
  }

  #modal_empty_cart .modal__title {
    font-size: 25px;
  }
}

@media screen and (max-width: 375.98px) {
